// 二次封装
import http from './http'
// 获取主题列表
// tab 类别
// page 页数
// limit 条数 默认40
const getTopics = ({ tab, page = 1, limit = 40 }) => {
  // get 请求传参 地址栏直接？ 或者
  // return http.get('/topics?tab=== &limit=== ')
  return http.get('/topics', { params: { tab, page, limit } })
}
const getTopicId = (id, accesstoken) => http.get(`/topic/${id}`, accesstoken ? { params: { accesstoken: accesstoken } } : {})
const getUserInfo = (loginname) => http.get(`/user/${loginname}`)
const login = (accesstoken) => http.post('/accesstoken', { accesstoken })

const collected = (accesstoken, topic_id) => {
  http.post('/topic_collect/collect', { accesstoken, topic_id })
}
const de_collected = (accesstoken, topic_id) => {
  http.post('/topic_collect/de_collect', { accesstoken, topic_id })
}
const zan = (reply_id, accesstoken) => {
 return http.post(`/reply/${reply_id}/ups`, { accesstoken })
}
const reply = (topic_id,accesstoken,content)=>http.post(`/topic/${topic_id}/replies`,{accesstoken,content})
const create = (accesstoken,title,tab,content)=>http.post('/topics',{accesstoken,title,tab,content})
const getMessageCount = (accesstoken)=> http.get('/message/count',{params:{accesstoken}})
const getMessage = (accesstoken)=>http.get('/messages',{params:{accesstoken}})
const markAllMessage  = (accesstoken)=>http.post('/message/mark_all',{accesstoken})


export { getTopics, getTopicId, getUserInfo, login, collected, de_collected, zan ,reply,create,getMessageCount,getMessage,markAllMessage}