<template>
  <div>
    <div v-if="zhuangtai">
      <topics-item v-for="topic in topics" :key="topic.id" v-bind="topic" />
      <el-pagination
        background
        layout="prev, pager, next"
        :page-size="40"

        :total="pageInfo[tab||'all'].total"
        
        :current-page='page'
        @current-change="currentChange"
      >
      </el-pagination>
    </div>
    <el-skeleton v-else :rows="6" animated />
  </div>
</template>

<script>
import { getTopics } from "../plugins/api";
import TopicsItem from "../components/TopicsItem.vue";
export default {
  components: { TopicsItem },
  data() {
    return {
      topics: [],
      zhuangtai: true,
      page:1,
      pageInfo: {
        all: {
          total: 240,
        },
        good: { total: 720 },
        share: { total: 160 },
        ask: { total: 80 },
        job: { total: 80 },
        dev: { total: 210 },
      },
    };
  },
  props: ["tab"],
  watch: {
    // "$route.params.tab": {
    //   async handler(val) {
    //     const res = await getTopics({ tab: val ||
    //      'all' });
    //     this.topics = res.data;
    //   },
    //   immediate: true,
    // },
    tab: {
      async handler(val) {
        this.zhuangtai = false;
        

        const page = this.$route.query.page

        const res = await getTopics({ tab: val || "all",page:page||1 });
        this.page = page*1 || 1 
        this.zhuangtai = true;
        this.topics = res.data;
      },
      immediate: true,
    },
  },
  methods: {
    async currentChange(page) {
      this.zhuangtai = false;
      const res = await getTopics({ tab:this.tab || "all",page:page });
      this.zhuangtai = true;
      this.topics = res.data;
      this.$router.push(this.$route.path +`?page=${page}`)
      this.page = page
      // console.log(this.page);
    },
  },
};
</script>

