<template>
  <div class="topics-itme">
    <router-link :to="`/user/${author.loginname}`">
      <img :src="author.avatar_url" alt="" />
    </router-link>
    <span :class="['count']" v-if="!isUser">
      <span>{{ reply_count }}</span>
      /
      <span>{{ visit_count }}</span>
    </span>
    <el-tag
      class="tag"
      size="mini"
      effect="dark"
      type="success"
      v-if="top || good"
      >{{ top ? "置顶" : "精华" }}</el-tag
    >
    <el-tag class="tag" size="mini" type="info" v-else-if="!isUser">{{
      tab | tabTextTransform
    }}</el-tag>

    <router-link class="title" :to="`/topic/${id}`"
      ><h4>{{ title }}</h4></router-link
    >
    <!-- '/topic/:id' -->
    <span>{{ last_reply_at | fromNow }}</span>
  </div>
</template>

<script>
export default {
  props: [
    "author",
    "good",
    "last_reply_at",
    "reply_count",
    "visit_count",
    "tab",
    "title",
    "top",
    "id",
    "isUser",
  ],
  computed: {},
};
</script>

<style lang="less" scoped>
.topics-itme {
  display: flex;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ccc;
  img {
    width: 40px;
    margin-right: 15px;
    border-radius: 4px;
  }
  .count {
    width: 80px;
    font-size: 12px;
    justify-content: center;
  }
  .tag {
    margin-right: 10px;
  }
  .active {
    background-color: #bb0606;
  }
  .title {
    flex-grow: 1;
    color: #000;
  }
}
</style>