<template>
  <div class="header">
    <div class="inner">
      <router-link to="/">
        <img
          src="https://static2.cnodejs.org/public/images/cnodejs_light.svg"
          alt=""
          class="logo"
          width="140px"
        />
      </router-link>
      <div class="header-right">
        <router-link to="/" class="home-link">首页</router-link>
        <el-badge :value="weidu" :hidden="weidu===0">
          <router-link to="/my/message">未读消息</router-link>
        </el-badge>

        <!-- <router-link to="/" v-if="userInfo">未读消息<router-link> -->
        <router-link to="/login" v-if="!userInfo" class="home-link">
          <el-button type="primary">登录</el-button>
        </router-link>
        <img v-if="userInfo" width="40" :src="userInfo.avatar_url" alt="" />
        <router-link to="/topics/create"
          ><el-button v-if="userInfo">新建</el-button></router-link
        >
        <el-button v-if="userInfo" type="primary" @click="logout"
          >退出</el-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { getMessageCount } from "../plugins/api";
export default {
  data() {
    return {
      userInfo: null,
      weidu: 0,
    };
  },
  methods: {
    logout() {
      sessionStorage.removeItem("userInfo");
      sessionStorage.removeItem("accesstoken");
      this.userInfo = null;
      this.$message({
        message: "退出成功",
        type: "success",
      });
      if (this.$route.path !== "/") {
        this.$router.push("/");
      }
    },
  },
  watch: {
    "$route.path": {
      async handler(val) {
        const userInfoJson = sessionStorage.getItem("userInfo");
        if (val === "/" && !this.userInfo && userInfoJson) {
          // console.log(123);
          this.userInfo = JSON.parse(userInfoJson);
          const res = await getMessageCount(
            sessionStorage.getItem("accesstoken")
          );
          this.weidu = res.data;
        }
      },
      immediate: true,
    },
  },
};
</script>
 
<style lang="less" scoped>
.header {
  width: 100%;
  height: 50px;
  background-color: #000;
  .inner {
    width: 1400px;
    margin: 0 auto;
    align-items: center;
    height: 50px;
    display: flex;
    justify-content: space-between;
  }
  .home-link {
    color: #fff;
    margin-right: 10px;
  }
}
</style>